.coin-change-page .title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.9375rem;
}
.coin-change-page .title .left {
  display: flex;
  align-items: center;
  font-size: 1.125rem;
  font-weight: 400;
  color: #ffffff;
}
.coin-change-page .title .left div:nth-child(2) {
  padding-left: 0.21875rem;
  padding-right: 0.21875rem;
  padding-top: 0.21875rem;
  padding-bottom: 0.21875rem;
  font-size: 0.625rem;
  margin-left: 0.5rem;
  border-radius: 2px;
  text-align: center;
  background: linear-gradient(90deg, #ff2ef3, #4f00ff);
}
.coin-change-page .title .right {
  font-size: 0.875rem;
  font-weight: 400;
  color: #00D2FF;
}
.coin-change-page .select-coin,
.coin-change-page .coin-address,
.coin-change-page .coin-num {
  padding-top: 0.9375rem;
  padding-bottom: 0.9375rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.coin-change-page .select-coin .am-list-item,
.coin-change-page .coin-address .am-list-item,
.coin-change-page .coin-num .am-list-item {
  background: rgba(255, 255, 255, 0);
  position: relative;
}
.coin-change-page .select-coin .am-list-item:after,
.coin-change-page .coin-address .am-list-item:after,
.coin-change-page .coin-num .am-list-item:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.coin-change-page .select-coin .am-list-item input,
.coin-change-page .coin-address .am-list-item input,
.coin-change-page .coin-num .am-list-item input {
  font-size: 0.75rem;
  color: #ffffff;
}
.coin-change-page .select-coin .am-list-item .am-input-extra,
.coin-change-page .coin-address .am-list-item .am-input-extra,
.coin-change-page .coin-num .am-list-item .am-input-extra {
  font-size: 0.75rem;
  font-weight: 600;
}
.coin-change-page .select-coin {
  padding: 0.9375rem;
}
.coin-change-page .select-coin .am-list-content {
  color: #666666 !important;
  font-size: 0.75rem;
}
.coin-change-page .select-coin .am-list-extra {
  font-size: 0.75rem;
  color: #fff;
}
.coin-change-page .select-coin .am-list-arrow-horizontal {
  font-size: 0.75rem;
  color: #fff;
}
.coin-change-page .select-coin .am-list-item {
  background: rgba(255, 255, 255, 0);
  position: relative;
}
.coin-change-page .select-coin .am-list-item:after {
  content: " ";
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  border-bottom: 1px solid #ffffff;
  color: #ffffff;
  transform-origin: 0 100%;
  transform: scaleY(0.5);
}
.coin-change-page .select-coin .am-list-item input {
  font-size: 0.75rem;
  color: #ffffff;
}
.coin-change-page .select-coin .am-list-item .am-input-extra {
  font-size: 0.75rem;
  font-weight: 600;
  color: #ffffff;
}
.coin-change-page .coin-address .am-input-extra,
.coin-change-page .coin-num .am-input-extra {
  color: #00D2FF;
}
.coin-change-page .address,
.coin-change-page .num {
  padding-left: 0.9375rem;
  margin-top: 0.3125rem;
  font-size: 0.875rem;
  color: #ffffff;
}
.coin-change-page .currant-coin {
  padding-left: 1.5625rem;
  font-size: 0.5625rem;
  color: #ffffff;
}
.coin-change-page .currant-coin .red {
  color: #d61919;
}
.coin-change-page .warn-box {
  margin-left: 1.5625rem;
  margin-right: 1.5625rem;
  margin-top: 1.5625rem;
  border-radius: 0.3125rem;
  padding: 0.9375rem;
  font-size: 0.75rem;
  position: relative;
  background: #111111;
  color: #ffffff;
}
.coin-change-page .warn-box:after {
  content: " ";
  position: absolute;
  left: 0;
  top: 0;
  width: 200%;
  height: 1px;
  border: 1px solid #666666;
  color: #666666;
  height: 200%;
  transform-origin: left top;
  transform: scale(0.5);
  border-radius: 0;
}
.coin-change-page .warn-box .yellow {
  color: #FF8400;
}
.coin-change-page .footerButton {
  margin-top: 2.5rem;
  padding-left: 1.5625rem;
  padding-right: 1.5625rem;
}
.am-picker-popup {
  background-color: #13141C !important;
}
.am-picker-popup .am-picker {
  background: #13141C !important;
}
.am-picker-popup .am-picker-popup-title {
  color: #fff;
}
.am-picker-popup .am-picker-col-mask {
  background-image: linear-gradient(to bottom, 13141C, 13141C), linear-gradient(to top, 13141C, 13141C) !important;
  background-image: #13141C !important;
  color: #fff !important;
}
.am-picker-popup .am-picker-col-item {
  color: #fff !important;
}
.am-picker-col-mask {
  background-image: linear-gradient(to bottom, 13141C, 13141C), linear-gradient(to top, 13141C, 13141C) !important;
  background-image: #13141C !important;
  display: none;
}
.am-picker-col-item {
  color: #fff !important;
}
